import  { Component } from "react";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import '../css/App.css';
import trophy from '../img/trophy.png'
type MyProps = { 
    scoreBoard?: any,
    totalScoreBoard?: any,
};
type MyState = { 
};
const theme = createTheme({
    palette: {
        mode: "dark"
    }
});
class ScoreBoard extends Component <MyProps, MyState>{
    
    constructor(props: MyProps) {
        super(props);
        this.state = {
        };
    }
    
    render() {
        const { scoreBoard, totalScoreBoard } = this.props;
        const scores = scoreBoard ? scoreBoard.scores : totalScoreBoard;
        let length = 14;

        return (
            <ThemeProvider theme={theme}>
                <Card sx={{ width: 400, maxWidth: 1, margin: 1}}>
                    <CardContent>
                        <Typography 
                            gutterBottom 
                            variant="h6" 
                            className="smile"
                            component="div">
                                <div >
                                    <span className = {scoreBoard ? 'roundHeader': (totalScoreBoard ? 'totalHeader': '')}> 
                                        {scoreBoard ? 'Runde resultat': (totalScoreBoard ? 'Toppliste': '')} 
                                    </span> 
                                </div>
                            </Typography>
                            { scores ? 
                                scores.map((userScore: any, index: number) => (  
                                    <Typography 
                                        key={userScore.userId}
                                        variant="body1" 
                                        color={index === 0 ? "#FFD700": (index === 1 ? "#C0C0C0": index === 2 ? "#CD7F32": 'nothing')}>
                                        <div className="scoreBoard">

                                            <div>
                                                { 
                                                    userScore.userName 
                                                    ? 
                                                    (index+1)+'. ' + userScore.userName.substring(0, length) + (userScore.userName.length >= length ? '...' : '')  
                                                    : 
                                                    'No results in yet.'
                                                } 
                                            </div>
                                            {
                                                (scoreBoard && userScore.score && index === 0) 
                                                ? 
                                                    <div><img src={trophy} className="trophyLogo" alt="logo" /></div>
                                                : 
                                                    (totalScoreBoard && userScore.trophies.length > 0) 
                                                    ?
                                                    
                                                        <div>
                                                            {userScore.trophies.map((t: any) => (  
                                                            <img src={trophy} className="trophyLogo" alt="logo" />
                                                        ))}
                                                        </div>
                                                    :
                                                        <div/>
                                            }

                                            <div>
                                            {userScore.score ? userScore.score : 'X'} 
                                            </div>
                                        </div>
                                    </Typography>
                                ))  : 
                                '' 
                            }
                    </CardContent>
                </Card>
            </ThemeProvider>
        );
    }
}

export default ScoreBoard;

